import React, { useState } from 'react';
import axios from 'axios';
import { getAuth, updateProfile } from 'firebase/auth';
import { Cloudinary } from '@cloudinary/url-gen';
import { AdvancedImage } from '@cloudinary/react';
import { auto } from '@cloudinary/url-gen/actions/resize';
import { autoGravity } from '@cloudinary/url-gen/qualifiers/gravity';

const UploadProfilePicture = () => {
    const [imageUrl, setImageUrl] = useState('');
    const [previewImg, setPreviewImg] = useState(null);
    const [file, setFile] = useState(null);
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');
    const auth = getAuth();
    const user = auth.currentUser;

    const handleFileChange = (e) => {
        const selectedFile = e.target.files[0];
        if (selectedFile && selectedFile.type.startsWith("image/")) {
            setFile(selectedFile);
            setPreviewImg(URL.createObjectURL(selectedFile));
        }
    };

    const handleUpload = async () => {
        if (!file) return;
        setLoading(true);
        setMessage('');

        const formData = new FormData();
        formData.append("file", file);
        formData.append("upload_preset", "YOUR_UPLOAD_PRESET"); // <-- Da creare su Cloudinary

        try {
            const response = await axios.post(
                "https://api.cloudinary.com/v1_1/YOUR_CLOUD_NAME/image/upload",
                formData
            );
            const uploadedUrl = response.data.secure_url;

            // Aggiorna profilo Firebase
            if (user) {
                await updateProfile(user, {
                    photoURL: uploadedUrl
                });
                setImageUrl(uploadedUrl);
                setMessage("Profilo aggiornato con successo!");
            }
        } catch (error) {
            console.error(error);
            setMessage("Errore durante il caricamento.");
        } finally {
            setLoading(false);
        }
    };

    // Visualizzazione immagine con SDK Cloudinary
    const cld = new Cloudinary({ cloud: { cloudName: 'YOUR_CLOUD_NAME' } });
    const cldImg = imageUrl
        ? cld.image(imageUrl.split('/').slice(-1)[0].split('.')[0])
            .format('auto')
            .quality('auto')
            .resize(auto().gravity(autoGravity()).width(500).height(500))
        : null;

    return (
        <div>
            <h3>Carica immagine profilo</h3>
            <input type="file" accept="image/*" onChange={handleFileChange} />
            {previewImg && <img src={previewImg} alt="Anteprima" width={150} />}
            <br />
            <button onClick={handleUpload} disabled={!file || loading}>
                {loading ? "Caricamento..." : "Carica"}
            </button>
            {message && <p>{message}</p>}

            {cldImg && <AdvancedImage cldImg={cldImg} />}
        </div>
    );
};

export default UploadProfilePicture;
